.selftweet{
    width: 100%;
    padding: 10px 20px;
    border-bottom: 8px solid var(--twitter-background-color);
}

.selftweet__body{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: start;
        align-items: flex-start;
    border-bottom: 1px solid var(--twitter-background-color);
}

.selftweet__body__avatar{
    margin-top: 1rem;
}

.selftweet__tweet{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
        justify-content: space-between;
    -ms-flex-align: center;
        align-items: center;
}

.selftweet__tweet > p{
    padding: 10px 10px 0 0;
    font-weight: 800;
    font-size: 0.8rem;
}

.selftweet__body > textarea{
    -ms-flex: 1 1;
        flex: 1 1;
    height: 18rem;
    resize: none;
    border: none;
    outline: none;
    font-family: inherit;
    padding: 10px 20px;
    font-weight: 600;
    font-size: 1.4rem;
    color: rgb(31, 31, 31);
}

.selftweet__button{
    background-color: var(--twitter-color) !important;
    color: white !important;
    border-radius: 30px !important;
    border: none !important;
    font-family: inherit !important;
    text-transform: inherit !important;
    font-weight: 900 !important;
    height: 30px !important;
    margin-top: 20px !important;
    font-size: 1.4rem !important;
    -webkit-transition: all 0.2s linear !important;
    -o-transition: all 0.2s linear !important;
    transition: all 0.2s linear !important;
    -webkit-backface-visibility: hidden !important;
            backface-visibility: hidden !important;
}

.selftweet__button:hover{
    -webkit-transform: translateY(-1px) scale(1.01) !important;
        -ms-transform: translateY(-1px) scale(1.01) !important;
            transform: translateY(-1px) scale(1.01) !important;
    -webkit-transition: all 0.2s linear !important;
    -o-transition: all 0.2s linear !important;
    transition: all 0.2s linear !important;
}

.selftweet__button:active{
    -webkit-transform: translateY(1px) scale(1) !important;
        -ms-transform: translateY(1px) scale(1) !important;
            transform: translateY(1px) scale(1) !important;
    -webkit-transition: all 0.2s linear !important;
    -o-transition: all 0.2s linear !important;
    transition: all 0.2s linear !important;
}

.image__uploader{
    margin-right: 5px;
}

.image__uploader > input{
    display: none;
}

#image__uploader__label{
    color: var(--twitter-color);
    padding: 1rem;
    font-size: 4.4rem !important;
    -webkit-transition: all 0.2s linear !important;
    -o-transition: all 0.2s linear !important;
    transition: all 0.2s linear !important;
    border-radius: 100%;
    cursor: pointer;
}

#image__uploader__label:hover{
    background-color: #E8F5FE;
    -webkit-transition: all 0.2s linear !important;
    -o-transition: all 0.2s linear !important;
    transition: all 0.2s linear !important;
}

#image__uploader__label:active{
    background-color: #b3d9f5;
    -webkit-transition: all 0.2s linear !important;
    -o-transition: all 0.2s linear !important;
    transition: all 0.2s linear !important;
}

.selftweet__tweet__details{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
}

.selftweet__button:disabled{
    background-color: grey!important;
}

.selftweet__tweet__details > p{
    margin-right: 5px;
    padding-bottom: 5px;
    border-bottom: 1px solid #b3d9f5;
}


